
.sko-aut {
  width: 100%;
}
.sko-aut * {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.sko-aut-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 3px;
  /*padding: 0 5px;*/
}
.sko-aut-searching {
  border-radius: 3px 3px 0 0;
}
.sko-aut-inputs {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  padding: 0 5px;
}
.sko-aut-inputs input {
    border: none;
    width: 100%;
}
.sko-aut-inputs input:focus {
    outline: none;
}
.sko-aut-results {
  background: white;
  border: 1px solid #ccc;
  border-top: 0px;
  color: black;
  list-style-type: none;
  margin: 0px;
  max-height: 400px;
  overflow-y: auto;
  padding: 0px;
  position: absolute;
  width: auto;
  z-index: 1000;
}
.sko-aut-results-item-error {
  color: red;
}
.sko-aut-results-item {
  padding: 7px 10px;
  cursor: pointer;
}
.sko-aut-results-item:hover {
  background: rgba(0, 180, 255, 0.075);
}
.sko-aut-selected {
  background: rgba(0, 180, 255, 0.15);
}
.sko-aut-icon {
  height: 14px;
  width: 14px;
}
.sko-aut-box .sko-aut-icon.sko-aut-clear {
  margin-top: -20px;
}
.sko-aut-animate-spin {
  -webkit-animation: spin 2s infinite linear;
          animation: spin 2s infinite linear;
}



